<template>
  <div class="tools__wrap">
    <Tools v-if="!breakpoints.mobile.matches" :tools="tools"/>
  </div>
  <main
    class="main-container"
    :class="'main-container__group-' + pageGroup"
  >
    <slot />
  </main>
</template>

<script>
import { useGetters } from '@/shared/plugins/vuex'
import { MODULES_NAMES } from '@/shared/common/modules'
import Tools from '@/client/components/Tools/Tools'
import { useBreakpointsMedia } from '@/shared/plugins/breakpoints'
import { HEIGHT_GROUPS } from '@/client/common/heightGroups'
import { useRoute } from 'vue-router'
import { computed } from 'vue'

export default {
  name: 'MainContainer',
  components: { Tools },
  setup () {
    const route = useRoute()
    const {
      getTools: tools
    } = useGetters(MODULES_NAMES.TOOLS)
    const { breakpoints } = useBreakpointsMedia()

    const pageGroup = computed(() => {
      if (HEIGHT_GROUPS.first.includes(route.name)) {
        return 'first'
      }
      return 'second'
    })

    return {
      tools,
      pageGroup,
      breakpoints
    }
  }
}
</script>

<style lang="scss">
.main-container {
  position: relative;
  display: flex;
  flex-direction: column;
  min-width: 580px;
  max-width: 580px;
  //overflow-y: auto;

  background: #FFFFFF;
  border: 1px solid rgba(0, 0, 0, 0.1);
  box-sizing: border-box;
  box-shadow: 0 4px 80px rgba(0, 0, 0, 0.09);
  border-radius: 40px;

  overflow: auto;

  &__group-first {
    height: $firstGroupHeight;
  }
  &__group-second {
    height: $secondGroupHeight;
  }

  @media all and (max-width: 700px) {
    min-width: unset;
    width: 95%;
  }
}
.tools__wrap {
  position: relative;
  min-width: 580px;
  max-width: 580px;
}
</style>
